export default {
	data() {
		return {
			isLogin : false,
		}
	},
	onLoad() {
		var _isLogin = getApp().globalData.isLogin;
		if(_isLogin){
			this.isLogin = true;
		}
	},
	onShow() {
		var _isLogin = getApp().globalData.isLogin;
		if(_isLogin){
			this.isLogin = true;
		}
	},
	methods: {
		goTest(){
			uni.navigateTo({
				url: '../exam/exam',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		goLogin(){
			uni.navigateTo({
				url:'../login/login'
			})
		}
	}
}
